import React, { useState } from 'react';
import Box from '@mui/material/Box';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import InputLabel from '@mui/material/InputLabel';
// Example images


const portfolios = ['SMALL LoBM', 'ME1 BM2', 'ME1 BM3', 'ME1 BM4', 'SMALL HiBM', 'ME2 BM1',
    'ME2 BM2', 'ME2 BM3', 'ME2 BM4', 'ME2 BM5', 'ME3 BM1', 'ME3 BM2',
    'ME3 BM3', 'ME3 BM4', 'ME3 BM5', 'ME4 BM1', 'ME4 BM2', 'ME4 BM3',
    'ME4 BM4', 'ME4 BM5', 'BIG LoBM', 'ME5 BM2', 'ME5 BM3', 'ME5 BM4',
    'BIG HiBM']

const baseDirectory = './figs/';

const post  = 'vixdaily.png'


const images =   portfolios.reduce((dict, key) => {
    dict[key] = './figs/' + String(key) +String(post);
    return dict;
  }, {});


const OptionBox = () => {
  const [selectedOption, setSelectedOption] = useState(require('./figs/SMALL LoBMvixdaily.png'));
  const handleChange = (event) => {
    setSelectedOption(event.target.value);
  };

  
  return (
    <Box>
      <FormControl variant="outlined" sx={{ minWidth: 200, mb: 2 }}>
        <InputLabel id="portfolio-select-label">Select Portfolio</InputLabel>
        <Select
          labelId="portfolio-select-label"
          value={selectedOption}
          onChange={handleChange}
          label="Select Portfolio"
        >
          {portfolios.map((s, index) => (
            <MenuItem key={index} value={require('./figs/' + String(s) + String(post))}>
              {s}
            </MenuItem>
          ))}
        </Select>
      </FormControl>

      <Box>
        {selectedOption && (
          <img src={selectedOption} alt="Selected" width={700} height={350} />
        )}
      </Box>
    </Box>
  );
};

export default OptionBox;
