import React, { useState } from 'react';
import PropTypes from 'prop-types';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import ReactMarkdown from 'react-markdown';
import OptionBox from './OptionBox';


function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};

function a11yProps(index) {
  return {
    id: `vertical-tab-${index}`,
    'aria-controls': `vertical-tabpanel-${index}`,
  };
}

export default function VerticalTabs() {
  const [value, setValue] = useState(0);
  
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Box sx={{ bgcolor: 'background.paper', display: 'flex', height: '100vh'  , width :'100vw'}}>
 
        <Tabs
          orientation="vertical"
          value={value}
          onChange={handleChange}
          aria-label="Vertical tabs example"
          sx={{ borderRight: 1, borderColor: 'divider', width: 200 }}
        >
          <Tab label="Introduction" {...a11yProps(0)} />
          <Tab label="Methodology" {...a11yProps(1)} />
          <Tab label="Result" {...a11yProps(2)} />
        </Tabs>
 
      
        <TabPanel value={value} index={0}>
          <ReactMarkdown>{`# Introduction\nThis is the introduction content.`}</ReactMarkdown>
        </TabPanel>
        <TabPanel value={value} index={1}>
          <ReactMarkdown>{`# Methodology\nThis is the methodology content.`}</ReactMarkdown>
        </TabPanel>
        <TabPanel value={value} index={2}  >
        <Box sx={{display: 'flex' ,justifyContent: 'center' , width: '100%'}}>
        <OptionBox/>
        </Box>
        </TabPanel>
 
    </Box>
  );
}
